<template>
  <div class="ao-person-zhsz">
    <div class="header">
      <img :src="iconaq" />
      <div class="info">
        <div class="msg">{{ msg }}</div>
        <div class="progress">
          <div class="progress-load" :style="{'width': width+'%'}"></div>
        </div>
        <div class="msg msg-2">{{ msg2 }}</div>
      </div>
    </div>
    <!-- detail -->
    <div class="detail">
      <div class="detail-item" v-for="(item, index) in detailList" :key="index">
        <img :src="item.icon" />
        <span class="title">{{ item.title }}</span>
        <span class="value">{{ item.value }}</span>
        <span v-if="item.btn" class="btn" :class="[item.class]" :data-clipboard-text="item.value" @click="path(item.path, item.event)">{{ item.btn }}</span>
      </div>
    </div>
  </div>
</template>

<script>
import Clipboard from 'clipboard'
import { mapState } from 'vuex'
import iconaq from "@/assets/icon-aq.png";
import iconchecked from "@/assets/icon-checked.png";
import iconcheck from "@/assets/icon-check.png";

import { getLevel } from '@/api/user'

export default {
  name: "AOAssetReturn",
  data() {
    return {
      iconaq: iconaq,
      iconchecked: iconchecked,
      width: 0,
      msg_txt: this.$t('person.zhsz_txt'),
      msg: '',
      msg2: '',
      detailList: this.$t('person.zhsz_detailList')
    };
  },
  computed: {
    ...mapState(['invitation_code', 'phone', 'transaction_password'])
  },
  mounted() {
    //this.detailList[0].value = this.invitation_code
    this.detailList[0].value = this.phone
    if(!this.phone){
      this.detailList[0].icon = iconcheck
      this.detailList[0].btn = this.detailList[0].btn2
      this.detailList[0].path = this.detailList[0].path2
    }
    if(this.transaction_password) {
      this.detailList[2].icon = iconchecked
    }
    getLevel().then(res => {
      this.width = res.level
      if (res.spt) {
        this.msg = this.msg_txt[0].replace('#',res.spt)
        this.msg2 = this.msg_txt[1].replace('#',res.spt)
      }else{
        res.spt = res.level > 60 ? this.$t('person.zhsz_m') : res.level > 40 ? this.$t('person.zhsz_n') : this.$t('person.zhsz_p')
        this.msg = this.msg_txt[0].replace('#',res.spt)
        this.msg2 = this.msg_txt[1].replace('#',res.spt)
      }
    })
  },
  methods: {
    copy() {  
      var clipboard = new Clipboard('.copy')  
      clipboard.on('success', e => {  
        this.$message({ message: "复制成功", type: "success" })
        // 释放内存  
        clipboard.destroy()  
      })  
      clipboard.on('error', e => {  
        // 不支持复制  
        this.$message({ message: "该浏览器不支持自动复制", type: "error" })
        // 释放内存  
        clipboard.destroy()  
      })  
    },
    path(path, event) {
      if (path) {
        this.$router.push({ path: path })
      }
      if (event) this[event]()
    }
  }
};
</script>

<style scoped lang="scss">
.ao-person-zhsz {
  .header {
    margin-bottom: 50px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    .info {
      margin-left: 30px;
    }
    .progress {
      width: 320px;
      height: 8px;
      border-radius: 4px;
      background-color: #254c84;
      margin: 22px 0 12px;
      overflow: hidden;
    }
    .progress-load {
      background-color: #5697f4;
      height: 100%;
      border-radius: 4px;
    }
    .msg {
      font-size: 16px;
      color: #fff;
    }
    .msg-2 {
      color: #637085;
      font-size: 14px;
    }
  }
  .detail {
    border-top: 1px solid #303b4b;
    color: #637085;
    font-size: 14px;
  }
  .detail-item {
    border-bottom: 1px solid #303b4b;
    line-height: 72px;
    position: relative;
    img {
      width: 16px;
      vertical-align: middle;
    }

    .title {
      margin-left: 20px;
    }
    .value {
      position: absolute;
      left: 300px;
      top: 0;
      color: #c7cce6;
    }
    .btn{
      color: #7a98f7;
      cursor: pointer;
      float: right;
    }
  }
}
</style>



